@font-face
{
font-family: ogg;
src: url('fonts/Ogg-Roman.woff2');
}

.App {
  text-align: center;
  font-family: Ogg, Garamond, "Times New Roman", serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 1s linear;
          animation: App-logo-spin infinite 1s linear;
  height: 80px;
}

.App-header {
  background-color: #000;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.02); transform: scale(1.02); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes App-logo-spin {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% { -webkit-transform: scale(1.02); transform: scale(1.02); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}



